<!-- 根据用户传递过来的值，同时设置了向上和向下两个缓冲区，确保不管是向上滚动还是向下滚动都能确保非常顺畅的用户体验 -->
<template>
    <scroll-view
        class="scroll-container"
        scroll-x="false"
        scroll-y="true"
        :refresherEnabled="true"
        :refresherTriggered="refresherTriggered"
        refresherBackground="#f5f7f9"
        :enhanced="true"
        :pagingEnabled="true"
        :scroll-top="topDistance"
        @scroll="handleScroll"
        @refresherrefresh="onRefresh"
        ref="scrollContainer">
        <div class="warpper" :style="paddingStyle" ref="wapperBox" id="elId">        
            <div v-for="(item,index) in needReanderList" :key="index">
                <!-- 列表元素占位 -->
                <slot name="item-content" :thisItem="item"></slot>
            </div>
            <!-- 列表前后占位 -->
            <slot name="after"></slot>
            
            <!-- 底部文案 -->
            <div v-if="onRequesting">
                <div class="loading-text" :class="onRequesting?'textBottom':'textLoading'">{{msg}}</div>
            </div>
        </div>
    </scroll-view>
    <!-- 一键置顶 -->
    <div v-show="isShowBtn" class="goTop" @click="backTop" :style="`right: ${ 10 - tabindex*windowWidth }px;`">
        <!-- <nut-icon size="22" name="top"></nut-icon> -->
        顶部
    </div>
</template>

<script>
// import Taro from "@tarojs/taro"

export default {
    props: {
        //所有获取数据列表
        allDataList: { default: () => [], type: Array },
        //上下缓存区数目
        bufferSize: { default: () => 5, type: Number },
        // 滚动区高度
        scrollHeight: { default: () => window.innerHeight, type: Number },
        //单行元素显示高度
        blockHeight: { default: () => 100, type: Number },
        //是否正在请求数据状态
        onRequesting: { default: () => true, type: Boolean },
        //数据加载显示文案
        msg: { default: () => "玩命加载中...", type: String },
        refresherTriggered: {
            type: Boolean,
            default: false
        },
        // tab索引
        tabindex: {
            type: Number,
            default: 0
        }
    },
    setup() {
        return {}
    },
    data() {
        return {
            //当前块所在的位置索引
            currentBlockIndex: 0,
            // //记录当前行模块的偏移量
            offsetBlock: 0,
            //用来记录准确的当前block定位
            rebackCurrentBlockIndex: 0,
            scrollTop: 0,
            topDistance: 0, // 滚动条距离顶部距离
            bufferChangeTag: true,
            isShowBtn: false, // 一键置顶按钮
            windowWidth: 375, // 屏幕宽度
        }
    },
    computed: {
        //距离顶部位移
        offsetTop() {
            if (this.currentBlockIndex < this.bufferSize) {
                return 0
            } else {
                return (this.currentBlockIndex - this.bufferSize) * this.blockHeight
            }
        },
        //距离底部位移
        offsetBottom() {
            if (this.currentBlockIndex < this.bufferSize) {
                return (
                    (this.allDataListLength - this.needRenderSize) * this.blockHeight
                )
            } else {
                return (
                    (this.allDataListLength - this.needRenderSize - this.currentBlockIndex + this.bufferSize) * this.blockHeight
                )
            }
        },
        //根据上下位padding值，计算待渲染样式
        paddingStyle() {
            return {
                "padding-top": this.offsetTop + "px",
                "padding-bottom": this.offsetBottom + "px"
            }
        },
        //根据屏幕容积数量和缓冲区数量，计算待渲染数据长度(这里要考虑上缓冲区在初始化的时候的需求)
        needRenderSize() {
            if (this.currentBlockIndex < this.bufferSize) {
                return this.currentBlockIndex + this.screenNum + this.bufferSize
            } else {
                return this.screenNum + this.bufferSize * 2
            }
        },
        //根据父组件传递的服务器拉取的数据，计算获取数据长度
        allDataListLength() {
            return this.allDataList.length
        },
        //根据顶部位移以及缓冲区相关，计算带渲染数组的内容
        needReanderList() {
            let subArr = []
            if (this.currentBlockIndex < this.bufferSize) {
                subArr = this.allDataList.slice(0, this.needRenderSize)
            } else {
                subArr = this.allDataList.slice(
                    this.currentBlockIndex - this.bufferSize,
                    this.currentBlockIndex - this.bufferSize + this.needRenderSize
                )
            }
            return [...subArr]
        },
        // 一屏容纳元素的数量
        screenNum() {
            return ~~(this.scrollHeight / this.blockHeight) + 2 
        },

       
    },
    created() {
        // const systemInfo = getSystemInfoSync()
        // const { windowWidth } = systemInfo
        // this.windowWidth = windowWidth
        // console.log('systemInfo', systemInfo)
    },
    mounted() {
    //监听屏幕变化，动态获取当前屏幕最大容积数量
        this.myresize()
        window.onresize = this.myresize
        window.orientationchange = this.myresize
    },
    watch: {
        scrollHeight(val) {
            this.$refs.scrollContainer.style.height = val + "px"
        }
    },
    
    methods: {
        // 一键置顶
        backTop() {
            if (this.topDistance === 0) {
                this.topDistance = 0.1
            } else {
                this.topDistance = 0
            }
        },
        //监听屏幕变化，动态获取屏幕最大容积数量，直接使用对应渲染API体验效果更佳
        myresize() {
            this.$refs.scrollContainer.style.height = this.scrollHeight + "px"
        },
        //监听当前容器的滚动事件
        handleScroll(e) {
            if (e) {
                this.scrollTop = e.detail.scrollTop
                this.isShowBtn = e.detail.scrollTop > this.scrollHeight ? true : false
            }
            this.changeBufferneedReanderList()
            
            // 使用setTimeout节流，在真机上会卡顿
            // that.changeBufferneedReanderList()
            // if (this.bufferChangeTag) {
            //     this.bufferChangeTag = false
            //     let timer = setTimeout(() => {
            //         this.changeBufferneedReanderList()
            //         this.bufferChangeTag = true
            //         clearTimeout(timer)
            //     }, 30)
            // }
        },
        //根据滚动事件修正相应数据
        changeBufferneedReanderList() {
            //第一步，我们获取当前容器在scoll事件中距离顶部的位移
            let scrollHeight = this.scrollTop

            // 记录偏移量
            this.offsetBlock = scrollHeight % this.blockHeight

            //第二步，根据当前位移，获取到当前需要渲染的数据起点位移所在位置，两次取反-向下取整
            let currentBlockIndex = ~~(scrollHeight / this.blockHeight)
            this.rebackCurrentBlockIndex = currentBlockIndex
            // console.log('currentBlockIndex', this.currentBlockIndex, currentBlockIndex)

            //第三步：如果我们发现当前的偏移量发生了变化，说明需要更新整个needReanderList对应的值了，这里要做一个判断，如果当前值没有发生变化则不进行渲染，防止资源消耗提高性能
            if (this.currentBlockIndex === currentBlockIndex) return
            
            //第四步：判断是否到达底部，如果到达底部则触发新的数据请求
            if (currentBlockIndex + this.screenNum >= this.allDataListLength) {
                //如果用户滑动过快，因为截流函数100毫秒内，可能会导致很多值变化，offectBottom会因为计算属性而导致不合理变化，需要重新设置
                // this.$refs.wapperBox.style.paddingTop =
                //     (this.allDataListLength - this.bufferSize) * this.blockHeight
                this.$refs.wapperBox.style.paddingBottom = "0px"
                this.$emit("bottom")
            } else {
                //第五步：如果没有到达底部，我们只需要在这里修正其新的index即可，剩下的交给计算属性来完成更详细的细节工作
                this.currentBlockIndex = currentBlockIndex
            }
        },
        onRefresh() {
            this.$emit('onRefresh')
        }
    }
}
</script>

<style lang="scss">

.scroll-container {
position: relative;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  .warpper {
    height: 100%;
  }
  .loading-text {
    padding: 0;
    font-size: 14px;
    line-height: 1.5;
    text-align: center;
  }
  .textLoanding {
        padding: 20px 0;
    }

    .textBottom {
        padding: 30px 0;
    }
}
.goTop {
    position: absolute;
    bottom: 50px;
    right: 10px;
    width: 42px;
    height: 42px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border: 1px solid #ccc;
}
</style>
